* {
  font-family: 'Quicksand', sans-serif;
}

.slider-caption {
  font-size: 60px;
}

.hover-el:hover {
  color: #e57373;
  font-weight: bold;
  transition: all .3s ease-in-out;
}

.search-form {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 30px auto;
}

.search-form h3 {
  font-size: 18px;
}

.form-separator {
  font-size: 18px;
  margin-top: 50px;
}

.modal-div h4 {
  color: teal;
}

.modal-info {
  font-size: 20px;
  color: teal;
}

.modal-info a {
  color: #e57373;
  line-height: 2.7;
}

.modal-trigger {
  font-size: 20px;
}

@media screen and (max-width:575px) {
  .modal-div img {
    width: 80%;
  }
}